export default "mat-form-field,\ntextarea {\n  width: 100%;\n}\n\nmat-spinner {\n  margin: auto;\n  color: #970000;\n}\n\ninput[type=\"file\"] {\n  visibility: hidden;\n}\n\n.image-preview {\n  height: 5rem;\n  margin: 1rem 0;\n}\n\n.image-preview img {\n  height: 100%;\n}\n\n/* ---- */\n\nbody {\n  background-color: #100e17;\n  font-family: 'Open Sans', sans-serif;\n}\n\n.container {\n  position: absolute;\n  height: 300px;\n  width: 600px;\n  top: 200px;\n  left: calc(50% - 300px);\n  display: flex;\n}\n\n.card {\n  display: flex;\n  height: 280px;\n  width: 200px;\n  background-color: #17141d;\n  border-radius: 10px;\n  box-shadow: -1rem 0 3rem #000;\n/*   margin-left: -50px; */\n  transition: 0.4s ease-out;\n  position: relative;\n  left: 0px;\n}\n\n.card:not(:first-child) {\n    margin-left: -50px;\n}\n\n.card:hover {\n  transform: translateY(-20px);\n  transition: 0.4s ease-out;\n}\n\n.card:hover ~ .card {\n  position: relative;\n  left: 50px;\n  transition: 0.4s ease-out;\n}\n\n.title {\n  color: white;\n  font-weight: 300;\n  position: absolute;\n  left: 20px;\n  top: 15px;\n}\n\n.bar {\n  position: absolute;\n  top: 100px;\n  left: 20px;\n  height: 5px;\n  width: 150px;\n}\n\n.emptybar {\n  background-color: #2e3033;\n  width: 100%;\n  height: 100%;\n}\n\n.filledbar {\n  position: absolute;\n  top: 0px;\n  z-index: 3;\n  width: 0px;\n  height: 100%;\n  background: rgb(0,154,217);\n  background: linear-gradient(90deg, rgba(0,154,217,1) 0%, rgba(217,147,0,1) 65%, rgba(255,186,0,1) 100%);\n  transition: 0.6s ease-out;\n}\n\n.card:hover .filledbar {\n  width: 120px;\n  transition: 0.4s ease-out;\n}\n\n.circle {\n  position: absolute;\n  top: 150px;\n  left: calc(50% - 60px);\n}\n\n.stroke {\n  stroke: white;\n  stroke-dasharray: 360;\n  stroke-dashoffset: 360;\n  transition: 0.6s ease-out;\n}\n\nsvg {\n  fill: #17141d;\n  stroke-width: 2px;\n}\n\n.card:hover .stroke {\n  stroke-dashoffset: 100;\n  transition: 0.6s ease-out;\n}\n/*# sourceMappingURL=data:application/json;base64, */";